import { Breadcrumb, Button } from 'antd'
import UploadButton from "../../components/common/button/UploadButton";
import { FcUpload } from "react-icons/fc";
import { GrRevert } from "react-icons/gr";
import { RiDeleteBin5Line } from "react-icons/ri";
import { BiSolidMessageRoundedEdit } from "react-icons/bi";
import VFMProgress from "../../components/progress";
import SuccessModal from "../../components/common/modal/success";
import FormModal from "../../components/common/modal/form";

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '1px solid #6F6F6F',
    boxShadow: 24,
    p: 4,
};

function Progress() {
    const [open, setOpen] = useState(false);
    const [open1, setOpen1] = useState(false);
    const [chatRegister, setChatRegister] = useState(false);
    const { t, i18n } = useTranslation();
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

    const handleOpen1 = () => setOpen1(true);
    const handleClose1 = () => setOpen1(false);

    const closefun = () => {
        handleClose();
        handleOpen1();
    };

    return (
        <>
            {/* <Header page={"webscreens"} pagetitle={"Progress"} /> */}
            <div className="py-24">
                <section id="product-title" className="flex flex-col md:flex-row gap-10">
                    <div className="w-full md:w-2/3">
                        <Breadcrumb
                            className="p-5"
                            separator=">"
                            items={[
                                {
                                    title: 'TOP_PAGE',
                                    href: '/'
                                },
                                {
                                    title: 'ALL_ITEM',
                                },
                            ]}
                        />
                        <div className="bg-blue-600 text-white px-5 py-1 font-medium rounded-md text-sm mb-5">
                            <p className="md:p-4 py-4 text-xl">Kaiyo Aoki [ Eclipse of Elegance ] {t("Redy Made Fist Collections2024")}</p>
                        </div>
                        <div>
                            <div className="flex bg-badge text-white md:px-5 px-1">
                                <p className="p-3">購入日　2024/2/10 </p>
                                <p className="p-3">購入金額　＄920USD</p>
                            </div>
                            <div className="p-3 border shadow-md shadow-gray-300">
                                <div className="grid grid-cols-6 gap-5 p-3">
                                    <p className="col-span-3 border-blue-700">基本料金</p>
                                    <p className="col-span-3">購入日　2024/2/10 </p>
                                </div>
                                <div className="grid grid-cols-6 gap-5 p-3">
                                    <p>基本料金</p>
                                    <p className="col-span-2 font-bold text-gray-800">[ Eclipse of Elegance ] </p>
                                    <p>＄400 </p>
                                </div>
                                <hr />
                                <div className="flex gap-5">
                                    <p className="p-3">オプション料金</p>
                                </div>
                                <hr />
                                <div className="grid grid-cols-6 gap-5 p-3">
                                    <div></div>
                                    <p className="col-span-2 font-bold text-gray-800">[ PICTUER-UPPER BODY ] </p>
                                    <p className="col-span-1">＄30 </p>
                                    <p className="col-span-1">4 </p>
                                    <p className="col-span-1">＄120 </p>
                                </div>
                                <div className="grid grid-cols-6 gap-5 p-3">
                                    <p className="p-3"></p>
                                    <p className="col-span-2 font-bold text-gray-800">[ PICTUER-FULL LENGTH ] </p>
                                    <p className="col-span-1">＄75 </p>
                                    <p className="col-span-1">2 </p>
                                    <p className="col-span-1">＄150 </p>
                                </div>
                                <div className="grid grid-cols-6 gap-5 p-3">
                                    <p className="p-3"></p>
                                    <p className="col-span-2 font-bold text-gray-800">[ MOVIE-10minute ] </p>
                                    <p className="col-span-1">＄130 </p>
                                    <p className="col-span-1">1 </p>
                                    <p className="col-span-1">＄130 </p>
                                </div>
                                <hr />
                                <div className="grid grid-cols-5 p-5">
                                    <p className="col-span-1">ハッシュタグ</p>
                                    <div className="col-span-4">
                                        <TagGroup tags={["＃KAWAII", "#ANIME", "＃DRESS", "＃JAPAN"]} />
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                    <div className="w-full md:w-1/3">
                        <div className="mx-auto mt-9 p-4 shadow-md shadow-gray-400">
                            <div className="flex items-center gap-3 mb-5 md:flex-row flex-col">
                                <VFM_Avatar size={28} img={Ellipse2} alt="wait" />
                                <div>
                                    <h1 className="text-badge m-0">DESIGNER</h1>
                                    <h1>Kaiyo Aoki [ Eclipse of Elegance ] </h1>
                                </div>
                            </div>
                            <div>
                                <div className="p-3 border">
                                    <div className="grid grid-cols-6 gap-5 p-3">
                                        <p className="col-span-2 border-blue-700">REQUESTER</p>
                                        <p className="col-span-4">REQUESTER_USER000</p>
                                    </div>
                                    <div className="grid grid-cols-6 gap-5 p-3">
                                        <p className="col-span-2 border-blue-700">料金</p>
                                        <p className="col-span-4">＄920</p>
                                    </div>
                                    <div className="grid grid-cols-6 gap-5 p-3">
                                        <p className="col-span-2 border-blue-700">購入日</p>
                                        <p className="col-span-4">＄920</p>
                                    </div>
                                    <div className="grid grid-cols-6 gap-5 p-3">
                                        <p className="col-span-2 border-blue-700">オプション</p>
                                        <p className="col-span-4">６点</p>
                                    </div>
                                </div>

                            </div>
                            <div className="w-full">
                                <VFMProgress />
                            </div>
                        </div>
                    </div>
                </section>
                <section id="carosal-box" className="mt-10">
                    {!chatRegister ?
                        <div className="md:p-8 p-2">
                            <hr />
                            <p onClick={handleOpen} className="mt-2 text-blue-700 cursor-pointer title-message-box">{t("定型文登録はこちらから（３つまで登録可能です。）")}</p>
                            <p className="mt-2 title-message-box">{t("定型文１　はじめまして。購入させていただきました。TEST USER000 です。これ…")}</p>
                            <p className="mt-2 mb-10 title-message-box">{t("定型文２　納品ありがとうございます。確認いたしますので、いましばらくお待ちい…")}</p>
                            <ChatBox />
                        </div> : <div className="p-8">
                            <hr />
                            <div className="pl-14">
                                <p className="mt-0 title-message-box">{t("Delivery received")}</p>
                                <p className="m-0 font-bold pt-1">REQUESTER_USER000</p>
                            </div>
                            <div>
                                <img className="w-16" src={Ellipse5} alt="wait" />
                                <input className="continue_input_chat" placeholder="例）はじめまして。商品購入を検討しています。商品購入は可能でしょうか？s" />
                            </div>
                            <button onClick={() => setChatRegister(false)} className="bg-red-700 chat-send-btn">{t("Delivery receipt")}</button>
                            <p className="text-center title-message-box">{t("transaction completed")}</p>
                        </div>

                    }
                </section>
                <UploadButton />
                <hr className="mt-20 pb-5" />
                <div className="flex flex-row">
                    <Button type="default" icon={<FcUpload />} >
                        ファイルを道択
                    </Button>
                    <p className="ml-2">遛択されていません</p>
                </div>
                <div className="flex flex-wrap gap-5 justify-center mt-10 " >
                    <Button type="primary" icon={<GrRevert />} size="large" className="px-12 bg-black">
                        検収を差戻しする</Button>
                    <Button type="primary" icon={<GrCompliance />} danger size="large" className="px-12">
                        検収を完了する</Button>
                    <Button type="primary" icon={<BsFillSendCheckFill />} size="large" className="px-20">
                        送信する</Button>
                </div>
            </div>
            <FormModal open={open} onCancel={handleClose} onConfirm={closefun} />
            <SuccessModal open={open1}
                onCancel={handleClose1}
                title="定型文の登録が完了しました。"
            />
        </>
    );
}

export default Progress;


